$media-query-one: 1000px;
$media-query-two: 900px;

.home {
	@extend .standard__section;
	max-width: 100vw;
	width: 100vw;

	&__header {
		@extend .standard__header;
		margin: 0;
		height: calc(100vh - 118px);
		background-image: url('images/home-background.jpg');

		&__title {
			@extend .standard__title;
			max-width: 75%;
			width: 700px;
			background: rgba(0,0,0,0.7);
			border-radius: 10px;
		}
	}

	&__info {
		margin: 30px auto;
		max-width: 1200px;
		display: flex;
		justify-content: center;
		align-items: center;
		@media all and (max-width: $media-query-two) {
			flex-direction: column;
		}

		div {

			margin-top: 10px;
			text-align: center;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			flex: 1;

			@media all and (max-width: $media-query-two) {
				width: 100%;
			}

			.home__info__img {
				width: 212px;
				padding: 15px;
				border: 2px solid white;
				border-radius: 5px;
				cursor: pointer;

				&:hover {
					border: 2px solid $m5-red;
				}
			}

			h3 {
				margin: 15px;
				text-transform: uppercase;
			}

			p {
				margin-bottom: 15px;
				width: 80%;
			}

			button {
				@extend .standard-btn;
				width: 150px;
				height: 50px;
				text-transform: uppercase;
			}

			.home__info__btn--darker {
				background-color: rgb(49, 104, 104);
			}
		}
	}

	&__apn-logo {
		@extend .apn-logo;
	}

}
